.main {
  display: flex;
  flex-direction: column;
}

.main__container {
  position: relative;
  width: 85.7%;
  max-width: 1500px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 120px;
  padding-bottom: 60px;
  margin-left: auto;
  margin-right: auto;

  @include tablet {
    width: 90%;
    padding-top: 70px;
    padding-bottom: 35px;
  }

  @include mobile {
    width: 100%;
    padding-top: 60px;
  }

  &--products {
    min-height: 757px;

    @include tablet {
      min-height: 692px;
    }

    @include mobile {
      min-height: 400px;
    }
  }

  &--blog {
    padding-top: 50px;

    @include tablet {
      width: 88.2%;
      padding-top: 25px;
      padding-bottom: 10px;
    }

    @include mobile {
      width: 90.5%;
      padding-top: 20px;
      padding-bottom: 30px;
    }
  }

  &--faq {
    padding-top: 60px;

    @include tablet {
      padding-top: 10px;
    }

    @include mobile {
      padding-top: 31px;
    }
  }

  &--channel {
    padding-top: 50px;
    padding-bottom: 130px;

    @include tablet {
      padding-top: 39px;
      padding-bottom: 81px;
    }

    @include mobile {
      padding-top: 25px;
      padding-bottom: 70px;
    }
  }
}

.main__container > h2 {
  @include style-reset;
  font-family: $font-family-secondary;
  font-weight: 700;
  font-size: 34px;
  line-height: 44px;
  text-align: center;
  letter-spacing: 0.03em;
  margin-bottom: 24px;
  margin-right: auto;
  margin-left: auto;

  @include tablet {
    margin-bottom: 19px;
  }
}

.main__container--faq > h2 {
  margin-bottom: 48px;

  @include mobile {
    margin-bottom: 28px;
  }
}

.main__container > a {
  @include style-reset;
  text-align: center;
  color: $color-main-black;
  border-bottom: 1px solid $color-primary;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 59px;

  @include tablet {
    padding-bottom: 4px;
    margin-bottom: 50px;
  }

  @include mobile {
    margin-bottom: 40px;
  }
}

.main__container--blog > a {
  @include tablet {
    margin-bottom: 60px;
  }

  @include mobile {
    margin-bottom: 50px;
  }
}

.main__container--channel > a {
  margin-bottom: 68px;

  @include tablet {
    margin-bottom: 60px;
  }

  @include mobile {
    margin-bottom: 46px;
  }
}
//######## nojs for products

.main__container--nojs .btn__slider {
  display: none;
}

.main__container--nojs .products {
  overflow-x: scroll;
  overflow-y: hidden;
  scroll-behavior: smooth;
  scroll-snap-type: x mandatory;
  -webkit-overflow-scrolling: touch;
  scrollbar-width: none;
  padding: 0 9px;

  &::-webkit-scrollbar {
    display: none;
  }
}

.main__container--nojs .products__item {
  flex-shrink: 1;
  margin-right: 30px;

  scroll-snap-align: center;

  &:last-child {
    margin-right: 0;
  }
}

.main__container--nojs .products__pagination {
  display: none;
}
